import { Injectable } from '@angular/core';

// Custom Services import
import { AuthDetailsService } from '../services/auth-details.service';
import { UserService } from '../services/user.service';


@Injectable()
export class AuthService {
  constructor(private authDetailsService: AuthDetailsService, private userService: UserService) { }

  public init(customOptions) {
    this.authDetailsService.setAuthConstants(customOptions);
  };

  public isAuthenticated() {
    return this.authDetailsService.isAuthenticated();
  };

  // Get profile form server or JWT decode
  public getUserProfile(data) {
    if (data && data.hasOwnProperty('server') && data.server) {
      return this.userService.getUserFromServer();
    } else {
      return this.userService.getUserFromJwt();
    }
  };

  // Change Password
  public changePassword(data) {
    return this.userService.changePassword(data);
  };

  // Logout function
  public logout() {
    this.authDetailsService.deleteJWT();
  };
}
